import react, {useState, useEffect} from "react";
import { NavLink } from "react-router-dom";
import firebase from "../firebase";
import 'firebase/auth';
import s from "./Auth.module.css";

const Registr = () => {
    const [isModal, setIsModal] = useState(false);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    async function regUser(e, email, password){
        e.preventDefault();
        try {
            const data = await firebase.auth().createUserWithEmailAndPassword(email, password)
            .then( (userCredential) => {
                let user = userCredential.user;
                user.sendEmailVerification().then( () => {
                    console.log("emal sent");
                }).catch(function(error) {
                    console.log("bad");
                });
            })
            
        } catch (error) {
            console.log(error.message)
            throw error
        }
    }
    let sendEmail = () => {
        let user = firebase.auth().currentUser;
        user.sendEmailVerification().then( () => {
            console.log("emal sent");
        })
    }
    return(
        <>
        <div className={s.auth}>
            <div className={s.title}>Регистрация</div>
            <form onSubmit={(e) => regUser(e, email, password)} className={s.form}>
                <div className={s.inputs}>
                    <div className={s.inputTitle}>e-mail</div>
                    <input type="text" placeholder="Ваш e-mail" value={email} onChange={ (e) => setEmail(e.target.value)} className={s.input}/>
                    <div className={s.inputTitle}>пароль</div>
                    <input type="password" placeholder="Ваш пароль" value={password} onChange={ (e) => setPassword(e.target.value)} className={s.input}/>
                </div>
                <button className={`${s.button} ${s.log}`}>Зарегестрироваться</button>
                <button type="button" className={`${s.button} ${s.reg}`} onClick={ sendEmail }>Выслать письмо еще раз</button>
            </form>
        </div>
        </>
    )
}

export default Registr;