import react, {useEffect, useRef} from "react";
import firebase from "../firebase";
import {connect} from "react-redux";
import {updateMessages, updateCurrentUser} from "../redux/store";
import s from "./Messages.module.css";
import Message from "./Message";

export const db = firebase.firestore();
const Messages = (props) => {
    useEffect( () => {
      const unsubscribe = db.collection("messages").orderBy("date", "asc").limitToLast(15).onSnapshot(
       function(snapshot) {
         let messages = [];
         snapshot.forEach( doc => messages.push({ userId: doc.data().userId, message: doc.data().message, userEmail: doc.data().userEmail}) )
         props.updateMessages( messages );
      });
      return () => unsubscribe();
    }, [])

    let messagesDiv = useRef(null);
    if (messagesDiv.current) {
      setTimeout(() => {
        messagesDiv.current.scrollTop = messagesDiv.current.scrollHeight;
      }, 1);
    }

    return(
       <div className={s.messages} ref={messagesDiv}>
           {props.messages.map( (m) => 
           <Message 
           key={m.date} 
           userId={m.userId} 
           message = {m.message}
           currentUserId= {props.id} 
           userEmail = {m.userEmail}/> )}
       </div>
    )
}
let mapStateToProps = (state) => {
    return{
      messages: state.messages,
      id: state.currentUser.uid
    }
}
export default connect(mapStateToProps, {updateMessages})(Messages);